import { useState, useEffect, useContext } from "react"
import { useParams } from "react-router";
import { Formlink, Wrapper, Button, Input, Buttondivs, Userdiv, Alldiv, Styledlink } from "../Styles/ListStyles";
import { EventlistContext } from "../../context/Context";
import sendFetch from "../../utils/sendFetch";
import filterData from "../../utils/filterData";
import searchData from "../../utils/searchData"

const Eventlist = () => {

    const { id } = useParams()
    
    const [ events, setEvents ] = useState([])
    const [ filtered, setFiltered ] = useState([])

    const { eventlist } = useContext(EventlistContext);

    useEffect(() => {
        if (eventlist) {
            setEvents(eventlist.filter(event => event.tim == id))
            setFiltered(eventlist.filter(event => event.tim == id))
        }
    }, [eventlist])

    function deleteEvent(e) {
        const id = e.target.name
        sendFetch("/api/delete_event", "DELETE", { id })
        filterData(id, events)
        setFiltered([ ...events ])
    }
    
    function Searchbar(e) {
        const search = searchData(e, events, "eventi")
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Formlink to={{pathname:`/event_obrazac/${id}`}}>Dodaj event +</Formlink>

            <Input onChange={Searchbar} type="search" placeholder="Pretraži evente..." />

            <Alldiv>
            {filtered && filtered.length ? filtered.map(event => {
                return (
                    <Userdiv key={event.id}>
                        <Styledlink to={{pathname:`/event/${event.id}`}}>
                            {event.ime_eventa} {event.ime_organizatora}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{pathname:`/event_obrazac/${id}/${event.id}`}} className="fas fa-edit"/>
                            <Button onClick={deleteEvent} name={event.id} className="fas fa-trash-alt" />
                        </Buttondivs>
                    </Userdiv>
                )
            })

            :

                <Userdiv>
                    Nema eventa za prikazati...
                </Userdiv>
            }
            </Alldiv>
        </Wrapper>
    )
};

export default Eventlist;
